import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	
	data() {
		return {
			bothIndex:'',
		    both:['甲方','乙方'],
		    finish:false,
		    fileList:[], //图片本地地址列表
		    imgArr:[], //上传阿里后的列表地址
		    cname: '',
		    money: '',
			clubList:[],
			clubName:'',
			club_id:''
		}
	},
	watch: {
	    finish () {
	      if (this.finish) {
			this.imgArr = JSON.stringify(this.imgArr)
	        uni.hideLoading();
	        this.$api.Turnover_store({
	          token: uni.getStorageSync("token"),
	          user_id: uni.getStorageSync("userId"),
	          trade:this.bothIndex,
			  company_name:this.cname,
			  amount:this.money,
	          imgs: this.imgArr,
			  club_id:this.club_id
	        }).then(res => {
	          if (res.data.code === '200') {
				  Toast('上传成功')
				  setTimeout(function() {
					  uni.reLaunch({
					  	url:'../../pagesTwo/TradingDetail/TradingDetail'
					  })
				  }, 1000);
	          }
	        })
	      }
	    }
	},
	mounted() {
	  	
	},
	onLoad() {
		this.getClub()
	},
	methods: {
		keypress () {
			setTimeout(() => { this.money = this.money.replace(/[^\d^.]+/g, ' ') }, 0)
		},
		pick(e){
			// console.log(e.detail.value)
			this.club_id = this.clubList[e.detail.value].club_id
			this.clubName = this.clubList[e.detail.value].name
		},
		bothclick(ind){
			// console.log(ind)
			this.bothIndex = ind
		},
		getClub(){
			this.$api.Club_club_list({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				this.clubList = res.data.data
				// console.log(this.clubList)
				var len = res.data.data.length
				if (len > 1) {
				  var result = []
				  var obj = {}
				  for (var i = 0; i < len; i++) {
					if (!obj[this.clubList[i].club_id]) {
					  result.push(this.clubList[i])
					  obj[this.clubList[i].club_id] = true
					}
				  }
				  this.clubList = result
				} else {
				  this.clubList = res.data.data
				}
			})
		},
		// 主图下面的图片
		sendTwo(){
			var that = this
			async function put () {
			  for (let i = 0; i < that.fileList.length; i++) { 
					  await  uni.uploadFile({
								  url: app.aliUrl,
								  filePath: that.fileList[i].url,
								  name: 'file',
								  formData: {
								    key:"wx/"+"${filename}",
								    signature: app.signature,
								    policy:app.policy,
								    OSSAccessKeyId:app.OSSAccessKeyId,
								    success_action_status:'201'
								  }
						 }).then(res=>{
							 that.imgArr.push({
								 url_image:res[1].data.split('<Location>')[1].split("</Location>")[0]
							 })
							 if(that.fileList.length === that.imgArr.length){
								 that.finish = true
							 }
							 
						 })
		          }
			}
			 put()
		},
		send () {
		      // 有俱乐部必须携带俱乐部
		      if (this.clubList.length > 0) {
		        if (this.money && this.bothIndex && this.cname && this.clubName) {
		          // 看看有没有图片
		          if (this.fileList.length > 0) {
					  Dialog.confirm({
					    title: '发布提示',
					    message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担。平台审核中发现问题会立即下架您所发布的内容，确认发布？'
					  }).then(() => {
					    uni.showLoading({
					        title: '加载中'
					    });
						this.sendTwo()
					 })
		           
		          } else {
					  Dialog.confirm({
					     title: '发布提示',
					     message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担。平台审核中发现问题会立即下架您所发布的内容，确认发布？'
					   }).then(() => {
					     uni.showLoading({
					         title: '加载中'
					     });
					  	setTimeout(() => {
					  	  this.finish = true
					  	}, 1000)
					  })
		            
		          }
		        } else {
		          if (!this.money) {
					Toast('交易金额没有填写')
		            return
		          }
		          if (!this.bothIndex) {
					Toast('甲方乙方没有选择')
		            return
		          }
		
		          if (!this.cname) {
					Toast('对方公司没有填写')
		            return
		          }
		          if (!this.clubName) {
					Toast('俱乐部没有选择')
		          }
		        }
		      } else { // 没有俱乐部
		        if (this.money && this.bothIndex && this.cname) {
		          // 看看有没有图片
		          if (this.fileList.length > 0) {
						  Dialog.confirm({
							title: '发布提示',
							message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担。平台审核中发现问题会立即下架您所发布的内容，确认发布？'
						  }).then(() => {
							uni.showLoading({
								title: '加载中'
							});
							this.sendTwo()
						 })
		           
		          } else {
					  Dialog.confirm({
						 title: '发布提示',
						 message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担。平台审核中发现问题会立即下架您所发布的内容，确认发布？'
					   }).then(() => {
						 uni.showLoading({
							 title: '加载中'
						 });
						setTimeout(() => {
						  this.finish = true
						}, 1000)
					  })
		            
		          }
		        } else {
		          if (!this.money) {
		          	Toast('交易金额没有填写')
		            return
		          }
		          if (!this.bothIndex) {
		          	Toast('甲方乙方没有选择')
		            return
		          }
		          			
		          if (!this.cname) {
		          	Toast('对方公司没有填写')
		          }
		          
		        }
		      }
		    
		},
		// 删除图片
		del(e){
			this.fileList = this.fileList.filter((item,index)=> index!=e.detail.index)
		},
		// 图片上传后的预览
		afterRead(event) {
		      const { file } = event.detail;
		      // 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
		      this.fileList.push({url:file[0].path})
			  setTimeout(()=>{
				  uni.pageScrollTo({
				  	scrollTop:1000
				  })
			  }, 300);
		}
	}
}
